* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  transition: all .3s;
}

ul, li, a {
  text-transform: none;
  text-decoration: none;
  list-style-type: none;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  justify-content: center;
  align-items: center;
  display: flex;
}

.header__logo .header__img {
  width: 100%;
  height: 100%;
}

.header__list {
  display: flex;
}

.header .list__item {
  color: #060b35;
  text-transform: uppercase;
  white-space: nowrap;
  cursor: pointer;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14.06px;
  display: flex;
  position: relative;
}

.header .list__item:first-child {
  margin-left: 0;
}

.header .list__item[data-qa="nav-hover"]:hover, .header .is-active {
  color: #00acdc;
}

.header .is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.catalog {
  grid-template-columns: repeat(1);
  justify-content: center;
  align-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

